import axios from 'axios'
import {AuthModel} from '../models/AuthModel'
import {UserModel} from '../models/UserModel'
const API_URL = process.env.REACT_APP_API_URL || 'api'

export const GET_USER_BY_access_token_URL = `${API_URL}/user/user-by-token`
export const LOGIN_URL = `${API_URL}/auth/login`
export const REGISTER_URL = `${API_URL}/auth/register`
export const REQUEST_OTP_URL = `${API_URL}/auth/forget`
export const VERIFY_OTP_URL = `${API_URL}/auth/verify-otp`
export const RESET_PASSWORD_URL = `${API_URL}/auth/reset`
export const SOCIAL_LOGIN_URL = `${API_URL}/auth/social-login`

console.log('🚀 ~ file: AuthCRUD.ts:12 ~ REQUEST_PASSWORD_URL', REQUEST_OTP_URL)
async function fun(email: string, password: string) {
  const res = await axios.post(LOGIN_URL, {email, password})
  console.log(res.data.message)
}
// Server should return AuthModel
export function login(email: string, password: string) {
  fun(email, password)
  return axios.post(LOGIN_URL, {email, password})
}

export async function socialLogin(access_token: string, provider: string) {
  return await axios.post(SOCIAL_LOGIN_URL, {
    accessToken: access_token,
    socialProvider: provider,
  })
}

// Server should return AuthModel
export function register(
  email: string,
  userName: string,
  firstName: string,
  lastName: string,
  password: string
) {
  return axios.post<AuthModel>(REGISTER_URL, {
    email,
    userName,
    firstName,
    lastName,
    password,
  })
}
// Server should return object => { result: boolean } (Is Email in DB)
export function requestOTP(email: string) {
  console.log('🚀 ~ file: AuthCRUD.ts:36 ~ requestPassword ~ REQUEST_PASSWORD_URL', REQUEST_OTP_URL)
  return axios.post<{result: unknown}>(REQUEST_OTP_URL, {email})
}
export function verifyOtp(otp: number, email: string) {
  console.log('🚀 ~ file: AuthCRUD.ts:36 ~ resetPassword ~ REQUEST_PASSWORD_URL', VERIFY_OTP_URL)
  return axios.post<{result: unknown}>(VERIFY_OTP_URL, {otp, email})
}

export function resetPassword(password: string, access_token: string) {
  console.log('🚀 ~ file: AuthCRUD.ts:36 ~ resetPassword ~ REQUEST_PASSWORD_URL', VERIFY_OTP_URL)
  return axios.post<{result: unknown}>(
    RESET_PASSWORD_URL,
    {password},
    {
      headers: {
        Authorization: 'Bearer ' + access_token,
      },
    }
  )
}

export function getUserByToken() {
  // Authorization head should be fulfilled in interceptor.
  // Check common redux folder => setupAxios
  return axios.get<UserModel>(GET_USER_BY_access_token_URL)
}
