import {takeEvery, put} from 'redux-saga/effects'
import {USERS_LIST, SET_USERS_LIST} from './usersconstant'

function* getUsers(): any {
  let data = yield fetch('Mock/dummyData.json')
  // let data = yield fetch('http://localhost:5500/user')
  data = yield data.json()
  // console.warn('Mock/dummyData.json', data)
  yield put({type: SET_USERS_LIST, data})
}

function* UsersSaga() {
  yield takeEvery(USERS_LIST, getUsers)
}

export default UsersSaga
