import {takeEvery, put} from 'redux-saga/effects'
import {PROJECTS_LIST, SET_PROJECTS_LIST} from './Projectsconstant'

function* getProjects(): any {
  // let data = yield fetch('Mock/dummyData.json')
  let data = yield fetch('Mock/MOCK_PROJECTS.json')
  data = yield data.json()
  console.warn('Mock/dummyData.json', data)
  yield put({type: SET_PROJECTS_LIST, data})
}

function* productSaga() {
  yield takeEvery(PROJECTS_LIST, getProjects)
}

export default productSaga
