import {Action} from '@reduxjs/toolkit'
import {persistReducer} from 'redux-persist'
import storage from 'redux-persist/lib/storage'
import {put, takeLatest} from 'redux-saga/effects'
import {UserModel} from '../models/UserModel'
import {getUserByToken} from './AuthCRUD'
import { Socket, io } from 'socket.io-client'
const SOCKET_URL=process.env.SOCKET_IO_URL || 'http://localhost:3001'
export interface ActionWithPayload<T> extends Action {
  payload?: T
}

export const actionTypes = {
  Login: '[Login] Action',
  Logout: '[Logout] Action',
  Register: '[Register] Action',
  UserRequested: '[Request User] Action',
  UserLoaded: '[Load User] Auth API',
  SetUser: '[Set User] Action',
  SetError: '[Set Error] Action',
  ConnectSocket: '[Connect Socket] Action',
}

const initialAuthState: IAuthState = {
  user: undefined,
  access_token: undefined,
  error: undefined,
  socket:undefined,
  role:undefined

}

export interface IAuthState {
  user?: UserModel
  access_token?: string
  error?: string
  socket?:Socket
  role?:string
}

export const reducer = persistReducer(
  {storage, key: 'auth', whitelist: ['user', 'access_token']},
  (state: IAuthState = initialAuthState, action: ActionWithPayload<IAuthState>) => {
    switch (action.type) {
      case actionTypes.Login: {
        const access_token = action.payload?.access_token
        console.log(actionTypes.Login)
        return {access_token}
      }

      case actionTypes.SetError: {
        const error = action.payload?.error
        // console.log(actionTypes.Login)
        return {...state, error}
      }

      case actionTypes.Register: {
        const access_token = action.payload?.access_token
        return {access_token, user: undefined}
      }

      case actionTypes.Logout: {
        return initialAuthState
      }

      case actionTypes.UserRequested: {
        return {...state, user: undefined}
      }

      case actionTypes.UserLoaded: {
        const user = action.payload?.user
        return {...state, user}
      }

      case actionTypes.SetUser: {
        const user = action.payload?.user
        console.log(user)
        return {...state, user}
      }
      case actionTypes.ConnectSocket: {
        const role=action.payload?.role
        const socket=io(`${SOCKET_URL}/${role}`,{transports:['websocket']})
        console.log(`conenction established for ${role} ${socket.on('connect',()=>console.log('conencted'))}`)
        return {...state, socket: socket}
      }

      default:
        return state
    }
  }
)

export const actions = {
  login: (access_token: string) => ({
    type: actionTypes.Login,
    payload: {access_token},
  }),
  connectSocket: (role:string) => ({
    type: actionTypes.ConnectSocket,
    payload: {role},
  }),

  register: (access_token: string) => ({
    type: actionTypes.Register,
    payload: {access_token},
  }),

  logout: () => ({
    type: actionTypes.Logout,
  }),
  setError: (error: string) => ({
    type: actionTypes.SetError,
    payload: {error},
  }),

  requestUser: () => ({
    type: actionTypes.UserRequested,
  }),

  fulfillUser: (user: UserModel) => ({
    type: actionTypes.UserLoaded,
    payload: {user},
  }),
  setUser: (user: UserModel) => ({type: actionTypes.SetUser, payload: {user}}),
}

export function* saga() {
  yield takeLatest(actionTypes.Login, function* loginSaga() {
    yield put(actions.requestUser())
  })

  yield takeLatest(actionTypes.Register, function* registerSaga() {
    yield put(actions.requestUser())
  })

  yield takeLatest(actionTypes.UserRequested, function* userRequested() {
    const {data: user} = yield getUserByToken()
    yield put(actions.fulfillUser(user))
  })
}
