import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {PageLink, PageTitle} from '../../../../_metronic/layout/core'
import {Private} from './components/Private'
import {Group} from './components/Group'
import {Drawer} from './components/Drawer'
import ChatRoom from './components/chat'
import AdminChatRoom from './components/adminChat'

const chatBreadCrumbs: Array<PageLink> = [
  // {
  //   title: 'Chat',
  //   path: '/apps/chat/private-chat',
  //   isSeparator: false,
  //   isActive: false,
  // },
  {
    title: '',
    path: '',
    isSeparator: true,
    isActive: false,
  },
]

const ChatPage: React.FC = () => {
  return (
    <Switch>
      <Route path='/apps/chat/private-chat'>
        <PageTitle breadcrumbs={chatBreadCrumbs}>Chat</PageTitle>
        <Private />
      </Route>
      <Route path='/apps/chat/chat'>
        <PageTitle breadcrumbs={chatBreadCrumbs}>Chat api</PageTitle>
        <ChatRoom />
      </Route>
      <Route path='/apps/chat/admin-chat'>
        <PageTitle breadcrumbs={chatBreadCrumbs}>Chat api</PageTitle>
        <AdminChatRoom />
      </Route>
      <Route path='/apps/chat/group-chat'>
        <PageTitle breadcrumbs={chatBreadCrumbs}>Group chat</PageTitle>
        <Group />
      </Route>
      <Route path='/apps/chat/drawer-chat'>
        <PageTitle breadcrumbs={chatBreadCrumbs}>Drawer chat</PageTitle>
        <Drawer />
      </Route>
      <Redirect from='/apps/chat' exact={true} to='/apps/chat/private-chat' />
      <Redirect to='/apps/chat/private-chat' />
      
    </Switch>
  )
}

export default ChatPage
