import {ADD_TO_PROJECTS, EMPTY_PROJECTS} from './Projectsconstant'
// import {  REMOVE_FROM_Projects } from './constant'

export const ProjectsData = (data = [], action: any) => {
  switch (action.type) {
    case ADD_TO_PROJECTS:
      console.warn('ADD_TO_Projects condition ', action)
      return [action.data, ...data]
    // case REMOVE_FROM_PROJECTS:
    //   console.warn('REMOVE_FROM_Projects condition ', action)
    //   data.length = data.length ? data.length - 1 : []
    //   return [...data]
    case EMPTY_PROJECTS:
      console.warn('EMPTY Projects condition ', action)
      data = []
      return [...data]
    default:
      return data
  }
}
